.body {
  font-family: Roboto;
  font-style: normal;
}

.flex--adjustment {
  width: 1040px;
  display: flex;
  flex-direction: row;
  margin-right: auto;
  margin-left: auto;
}

.form--list {
  list-style-type: none;
  padding: 0;
}
.form--row {
  display: flex;
  justify-content: flex-end;
  padding-top: 16px;
  padding-left: 16px;
  padding-right: 16px;
}

.form--row--article {
  display: flex;
  justify-content: space-around;
  justify-content: flex-end;
  padding-left: 16px;
  padding-right: 16px;
}

.form--row > label {
  padding-top: 16px;
  padding-left: 16px;
  padding-right: 16px;
  flex: 1;
  border-radius: 10px;
}
.form--row > input,
.form--row > option,
.form--row > select {
  flex: 2;
  border-radius: 10px;
  background: #f5f4f5;
  border: 2px solid #c4c4c4;
  box-sizing: border-box;
  padding-left: 32px;
}

.form--row--article > input,
.form--row--article > option,
.form--row--article > select {
  flex: 1;
  border-radius: 10px;
  background: #f5f4f5;
  border: 2px solid #c4c4c4;
  box-sizing: border-box;
  padding-left: 12px;
  width: 40px;
}

.dropdown--row {
  padding-top: 8px;
  padding-left: 16px;
  padding-bottom: 8px;
  padding-right: 16px;
  border: 2px solid #c4c4c4;
  flex: 1;
  border-radius: 10px;
  background: #f5f4f5;
  color: #786e7a;
}

.form--button {
  border-radius: 10px;
  background: #4a1256;
  padding: 10px 22px;
  color: #f5f4f5;
  outline: none;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  text-decoration: none;
  margin-left: 24px;
  margin-top: 12px;
}

.form--button:hover {
  transition: all 0.2s ease-in-out;
  background: #4a1256;
  background: #f5f4f5;
  color: #8d519a;
}

.form--button--idea {
  border-radius: 10px;
  background: #4a1256;
  padding: 10px 22px;
  color: #f5f4f5;
  outline: none;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  text-decoration: none;
  margin-left: 24px;
  margin-top: 36px;
}

.form--button--idea:hover {
  transition: all 0.2s ease-in-out;
  background: #4a1256;
  background: #f5f4f5;
  color: #8d519a;
}

.form--button--table {
  border-radius: 10px;
  background: #4a1256;
  padding: 4px 14px;
  color: #f5f4f5;
  outline: none;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  text-decoration: none;
}

.form--button--table:hover {
  transition: all 0.2s ease-in-out;
  background: #4a1256;
  background: #f5f4f5;
  color: #8d519a;
}

.bold {
  font-weight: bold;
}

.form--button--long {
  border-radius: 10px;
  background: #4a1256;
  padding: 10px 22px;
  color: #f5f4f5;
  outline: none;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  text-decoration: none;
  margin-left: 24px;
  margin-top: 12px;
}

.form--button--long:hover {
  transition: all 0.2s ease-in-out;
  background: #4a1256;
  background: #f5f4f5;
  color: #8d519a;
}

.form--button--long--today {
  border-radius: 10px;
  width: 220px;
  background: #5a535c;
  color: #f5f4f5;
  outline: none;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  text-decoration: none;
}

.form--button--long--today:hover {
  transition: all 0.2s ease-in-out;
  background: #f5f4f5;
  color: #5a535c;
}

.actionComponents {
  display: flex;
  flex-direction: row;
  gap: 60px;
}

.signOut {
  margin-left: 120px;
}

.overview--header {
  position: absolute;
  margin-left: 24px;
  top: 134px;
  height: 16px;
  padding-top: 20px;
  padding-bottom: 12px;
  font-weight: bold;
  font-size: 18px;
  line-height: 100%;

  display: flex;
  align-items: center;
  color: #786e7a;
}

.splitScreenLanding {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  gap: 50px;

  margin-top: auto;
  margin-bottom: auto;
}

.leftPaneLanding {
  width: 40%;
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.leftPaneLandingLogged {
  width: 40%;
  padding: 10px;
}

.rightPaneLanding {
  width: 60%;
  flex-wrap: wrap;
  border-radius: 10px;
  padding: 10px;
  margin: auto;
  margin-right: 30px;
  background: #f5f4f5;
}

#second {
  width: 75%;
  height: 50%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 10%;
}

#second--signup {
  width: 75%;
  height: 75%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 5%;
}

.landing--logged {
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  text-align: center;
  vertical-align: middle;
}

.landing--logged > p {
  padding-top: 144px;
  padding-bottom: 48px;
  color: #f5f4f5;
  font-size: 32px;
}

.button {
