tbody td {
  width: 15%;
}

thead,
tbody {
  display: block;
}

.tbody--articles {
  height: 720px;
  overflow-y: auto;
  overflow-x: hidden;
}
.tbody--ideas {
  height: 600px;
  overflow-y: auto;
  overflow-x: hidden;
}

.tbody--staff {
  height: 120px;
  overflow-y: auto;
  overflow-x: hidden;
}

.tbody--home {
  height: 280px;
  overflow-y: auto;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
  border-radius: 10px;
}

.me-2 {
  margin-left: 5px;
  margin-top: 5px;
}

.filter--rough {
  margin-right: 5px;
  margin-top: 5px;
}

.card {
  padding: 10px;
  margin: 8px 4px;
  -webkit-box-shadow: 3px 3px 3px 3px #000; /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
  -moz-box-shadow: 3px 3px 3px 3px #000; /* Firefox 3.5 - 3.6 */
  box-shadow: 3px 3px 3px 3px #000; /* Opera 10.5, IE 9, Firefox 4+, Chrome 6+, iOS 5 */
}

header {
  background-color: #ecf0f1;
}
h5 {
